import styled from 'styled-components'
import { Link } from 'gatsby'
import { useDispatch, useSelector } from 'react-redux'
import { setCartOpen } from '../actions'
// Styles
import { desktopVw, mobileVw } from '../styles/utils'

// Utils
import {
  capitalizeWord,
  decodeIdFromGraphQLResponse,
  extractLineItemFromGraphEdge,
  getHyphenatedColorName,
  getLineItemPropertiesFromProductMetaTags,
  getOvpdCustomAttribute,
  getPathName,
  getPathParams,
  getProductCarousel,
  getProductImagesOnSizeAndColor,
  getValidColorName,
} from '../utils'

// Hooks
import useShopifyProduct from '../hooks/useShopifyProduct'
import useSelectedOptions from '../hooks/useSelectedOptions'
import useCheckoutLineItem from '../hooks/useCheckoutLineItem'
import usePrice from '../hooks/usePrice'
import useGetProductReviewSummary from '../hooks/useGetProductReviewSummary'

// Components
import ScaledImage from './ScaledImage'
import Tag from './Tag'
import QuickShopButton from './QuickShopButton'
import QuickShopModal from './QuickShopModal'
import WishlistButton from './WishlistButton'
import { Select } from './FormElements'
import CartIcon from '../assets/cart.svg'
import eventTracker from '../trackers/EventTracker'
import EventType from '../trackers/eventType'
import ColorOption from './ColorOption'
import { withErrorBoundary } from './ErrorBoundary/ErrorBoundary'

const StyledPairWithThumbnail = styled.article`
  position: relative;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    &:hover {
      .quickshop-btn {
        transform: scale(1);
        opacity: 1;

        svg {
          opacity: 1;
        }
      }
    }
  }
`

const ImageWrapper = styled.div`
  position: relative;
  height: 347px;
  width: 282px;
  background: ${props => props.theme.colors.greyLighter};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: ${desktopVw(283)};
    width: 100%;
  }
`

const ButtonWrapper = styled.div`
  position: absolute;
  z-index: 1;
  right: 10px;
  top: 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    top: ${desktopVw(22)};
    right: ${desktopVw(20)};
  }
`

const StyledWishlistButton = styled(WishlistButton)`
  width: 13px;

  @media (min-width: ${props => props.theme.breakpoints.desktop}px) {
    width: ${desktopVw(11)};
  }
`

