import { useEffect } from 'react'
import { decodeIdFromGraphQLResponse } from '../utils'
import useFetch from './useFetch'

const useGetProductReviewSummary = (productId, initiate) => {
  const { fetchData, response, error, loading } = useFetch()
  const url = process.env.GATSBY_STAMPED_REVIEWS_SUMMARY_URL

  useEffect(() => {
    if (!productId || !initiate) return

    const decodedProductId = decodeIdFromGraphQLResponse(productId)
    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        productIds: [
          {
            productId: decodedProductId,
          },
        ],
        apiKey: process.env.GATSBY_STAMPED_API_KEY_PUBLIC,
        storeUrl: process.env.GATSBY_STAMPED_STORE_URL,
      }),
    }

    fetchData(url, options)
  }, [productId, initiate])

  return { summaryResponse: response, error }
}

export default useGetProductReviewSummary
