import React, { useState } from 'react'
import styled from 'styled-components'

import AccordionItem from './AccordionItem'

const StyledAccordion = styled.div``

const Accordion = ({
  className,
  items,
  activeIndex,
  toggleMode = false,
  trackParams,
  htmlContent,
  showLabelNumber = false,
  openAll = false,
}) => {
  const [toggle, setToggle] = useState(activeIndex)

  return (
    <StyledAccordion className={className}>
      {items.map((item, i) => {
        let isOpen = Array.isArray(activeIndex)
          ? activeIndex.includes(i)
          : toggle === i
        if (openAll) isOpen = true
        return (
          <AccordionItem
            key={`${item.id}${i}`}
            title={item.title}
            json={item.text && item.text.json}
            open={isOpen}
            toggle={toggleMode ? () => setToggle(i) : null}
            trackParams={trackParams}
            index={i}
            showLabelNumber={showLabelNumber}
            htmlContent={htmlContent}
          />
        )
      })}
    </StyledAccordion>
  )
}

export default Accordion
