import React, { useState, useRef, useEffect } from 'react'
import styled from 'styled-components'

import { desktopVw } from '../styles/utils'
import theme from '../styles/theme'

import RichText from './RichText'
import PlusIcon from '../assets/plus.svg'
import eventTracker from '../trackers/EventTracker'
import EventType from '../trackers/eventType'
import BlackCircle from '../static-svg/black-circle.svg'

const StyledAccordionItem = styled.div`
  border-bottom: 1px solid ${props => props.theme.colors.greyLighter};
`

const AccordionLabelContainer = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 0;
  cursor: pointer;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(20)} 0
      ${props => (props.decreasePadding ? desktopVw(9) : desktopVw(22))};
    transition: padding 0.3s ease-out;
  }
`

const Label = styled.h2`
  max-width: 291px;
  font-size: 18px;
  line-height: 20px;
  letter-spacing: 0.2px;
  cursor: pointer;
  font-weight: normal;
  font-family: ${props => props.theme.fonts.primary};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(11)};
    line-height: ${desktopVw(20)};
    font-family: ${props => props.theme.fonts.primary};
    text-transform: uppercase;
  }
`

const Body = styled.div`
  max-height: var(--maxHeight, '100%');
  padding-bottom: var(--paddingBottomMobile);
  transition: max-height 0.3s ease-out;
  overflow: hidden;

  p:last-child {
    margin: 0;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    max-height: var(--maxHeight);
    padding-bottom: var(--paddingBottomDesktop);
    padding-right: ${desktopVw(14)};
  }
`

const Minus = styled.div`
  height: 2px;
  background: black;
  width: 11px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: ${desktopVw(2)};
    width: ${desktopVw(11)};
  }
`

const StyledPlusIcon = styled(PlusIcon)`
  width: 11px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(11)};
  }
`

const HTMLRenderer = styled.div`
  ul {
    list-style-type: none;
    padding-left: 1em;
    font-size: 15px;
    li {
      list-style-image: url(${BlackCircle});
      color: ${props => props.theme.colors.darkGrey};
      font-size: 11px;
      line-height: 18px;
      margin: 0;
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    ul {
      font-size: ${desktopVw(15)};
      li {
        font-size: ${desktopVw(11)};
        line-height: ${desktopVw(18)};
      }
    }
  }
`

const AccordionItem = ({
  title,
  json,
  open = false,
  toggle,
  trackParams = {},
  htmlContent = {},
  showLabelNumber = false,
  index,
  className,
}) => {
  const [active, setActive] = useState(open)
  const [maxHeight, setMaxHeight] = useState(undefined)

  const ref = useRef(null)
  const titleRef = useRef(null)

  useEffect(() => {
    if (open) {
      setMaxHeight(ref.current.scrollHeight)
      setActive(open)
    } else {
      setActive(false)
    }
  }, [ref, open])

  const handleClick = () => {
    if (toggle) {
      toggle()
    } else {
      setMaxHeight(ref.current.scrollHeight)
      setActive(!active)
      eventTracker.trackClick(EventType.ACCORDION_TOGGLE, {
        ...trackParams,
        value: active ? 'collapsed' : 'expanded',
        label: title,
      })
    }

    if (!toggle) return

    const mq = window.matchMedia(
      `(min-width: ${theme.breakpoints.desktopSmall}px)`,
    )

    if (mq.matches) return

    const { header } = theme
    const offset = 20

    setTimeout(() => {
      window.scrollTo({
        top:
          titleRef.current.getBoundingClientRect().top +
          window.scrollY -
          header.mobile.height -
          offset,
        behavior: 'smooth',
      })
    }, 500)
  }

  return (
    <StyledAccordionItem className={`${className} accordion-item`}>
      <AccordionLabelContainer
        className='accordion-label-container'
        onClick={() => handleClick()}
        decreasePadding={active}
      >
        <Label ref={titleRef}>
          {showLabelNumber ? `${index + 1}.` : null} {title}
        </Label>
        <button aria-label={`${active ? 'close' : 'open'} ${title}`}>
          {active ? <Minus /> : <StyledPlusIcon />}
        </button>
      </AccordionLabelContainer>
      <Body
        style={{
          '--maxHeight': active ? (maxHeight ? `${maxHeight}px` : '100%') : 0,
          '--paddingBottomMobile': active ? '31px' : null,
          '--paddingBottomDesktop': active ? desktopVw(22) : null,
        }}
        ref={ref}
        className={`accordion-item__body ${active &&
          'accordion-item__body__active'}`}
      >
        {Object.keys(htmlContent).indexOf(title) > -1 && htmlContent[title] ? (
          <HTMLRenderer
            dangerouslySetInnerHTML={{ __html: htmlContent[title] }}
          />
        ) : (
          <RichText json={json} />
        )}
      </Body>
    </StyledAccordionItem>
  )
}

export default AccordionItem
